.first-level-style {
	-webkit-transition: all 0.4s ease;
	-o-transition: all 0.4s ease;
	transition: all 0.4s ease;
	font-size: 12px;
}
.second-level-style {
    margin-right: -26px;
    border-radius: 5px;
    border: 1px solid transparent;
    margin-left: -37px;
    background-color: transparent;
}
.second-level-style:focus {
	outline: none !important;
  }
