.accordion-open {
	-webkit-transition: all 4s ease;
	-o-transition: all 4s ease;
	transition: all 4s ease;
	display: block;
}

.accordion-open > div {
	background-color: #fafafa;
}

.accordion-close {
	-webkit-transition: all 4s ease;
	-o-transition: all 4s ease;
	transition: all 4s ease;
	display: none;
}

.dropdown:nth-child(3) {
	background-color: red !important;
}
