﻿.video-section {
	padding: 3em;
	background-color: #fafafa;

	.row {
		margin: auto;
		width: 90%;
	}
}

.video-container {
	position: relative;
	overflow-y: scroll;
	margin-top: 250px;
	height: 80vh;
}


.page-title {
	font-weight: bolder !important;
	color: #10A3FF !important;
	text-align: left;
	margin: 0;
	padding: 0;
	letter-spacing: -2px !important;
  }

.video-title h2 {
	font-weight: bolder;
	color: #5e5e5e;
}

.video-description h5 {
	line-height: 2;
	font-size: 12px;
}

.video-item h5 {
	font-weight: bolder;
	margin-top: 5px;
	color: #4d4d4d;
	line-height: 1.5;
	font-family: "work sans" !important;
}

.short-desc {
	font-size: 12px;
}

.videoList-container {
	height: 80vh;
	overflow-x: hidden;
	margin-top: 250px;
	overflow-y: scroll;
}

.videoList-container {
	.video-item {
		margin-bottom: 1.5em;

		&:hover {
			cursor: pointer;
		}
	}
}

.videoList-container > .video-item img {
	position: relative;
}

.tabs {
	width: 100%;
	display: block;
	margin: 10px;
	position: relative;
}

.tabs .tab {
	float: left;
	display: block;
}

.tabs .tab > input[type="radio"] {
	position: absolute;
	top: -9999px;
	left: -9999px;
}

.tabs .tab > label {
	display: inline-block;
	padding: 6px 6px;
	font-size: 10px;
	text-transform: uppercase;
	cursor: pointer;
	position: relative;
	color: rgba(7, 1, 1, 0.568);
}

.tabs .content .inn-content {
	width: max-content;
	// background-color: blue;
}

.tabs .content {
	z-index: 0; /* or display: none; */
	overflow-x: auto;
	overflow-y: auto;
	width: 100%;
	// background-color: red;
	padding: 25px 0;
	position: absolute;
	top: 27px;
	left: 0;

	opacity: 0;
	transition: opacity 400ms ease-out;
}

.tabs > .tab > [id^="tab"]:checked + label {
	top: 0;
	background: transparent;
	color: #0c0c0c;
}

.tabs > .tab > [id^="tab"]:checked ~ [id^="tab-content"] {
	z-index: 1; /* or display: block; */

	opacity: 1;
	transition: opacity 400ms ease-out;
}
@media only screen and (max-device-width: 450px) {
	.video-section {
		padding: 20px 0;
		.row {
			margin: auto;
			width: 100%;
		}
	}

	.videoList-container {
		margin-top: 10px;

		.video-item {
			margin-bottom: 1.5em;

			&:hover {
				cursor: pointer;
			}
		}
	}

	.searchpart {
		top: 200px;
	}

	.video-container {
		margin-top: 50px;
		height: max-content;
	}

	.page-title {
		font-size: 20px;
	}

	.tabs .tab > label {
		display: inline-block;
		padding: 5;
		font-size: 10px;
		font-weight: bold;
		text-transform: capitalize;
		color: rgba(7, 1, 1, 0.568);
	}
}
