﻿$sans: "work sans";

.padding-left0 {
  padding-left: 0 !important;
}

.padding-right0 {
  padding-right: 0 !important;
}

.hide-desktop {
  display: none;
  @media screen and (max-width: 769px) {
    display: block;
  }
}

.hide-mobile {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
  }
}
