﻿.register-wrapper {
  @media screen and (max-width: 768px) {
    height: 100% !important;
  }

  .containerr {
    //padding-top: 1%;
    margin: 0;
    padding-left: 0;
    padding-right: 0;
    overflow: hidden;
    height: 100vh;
    .package {
      // background-image: url("../../../img/bg-register.svg");
      // background-repeat: no-repeat;
      // background-size: cover;
      // position: relative;
      min-height: 100%;
      font-family: $sans !important;
      text-align: center !important;

      h2 {
        color: $white;
        font-weight: bolder;
        font-family: $sans;
      }

      h1 {
        color: #47b5fa;
        font-family: $bold-sans;
        font-weight: bolder;
        margin-top: 2em;
        margin-bottom: 1em;
      }
      h5 {
        color: #898989;
        font-family: $light-sans;
        font-weight: lighter;
        padding-left: 10em;
        padding-right: 10em;
        line-height: 1.5;
      }

      .slider-caption {
        position: absolute;
      }

      ul.slick-dots {
        bottom: 50px !important;
      }
      .packages {
        margin: 0;

        .package-content {
          @media screen and (min-width: 768px) {
            padding-top: 20px;
          }

          &:before {
            content: url("../../../img/check.png");
            position: absolute;
            bottom: 17px;

            @media screen and (max-width: 768px) {
              bottom: 36px;
            }
          }

          .wrap-text {
            display: inline-block;
            padding-left: 35px;
            margin-top: 10px;

            span.head {
              font-weight: bold;
              color: $white;
              line-height: 1.8;
            }

            span.desc {
              color: $white;

              @media screen and (max-width: 768px) {
                font-size: 11px !important;
              }
            }
          }
        }
      }
    }

    .register-details {
      background-color: #ffffff;
      font-family: "work sans", "Open Sans";
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;

      width: 100%;

      h3 {
        font-weight: bolder;
        color: $studyblue;
      }
      form {
        padding-left: 7em;
        padding-right: 7em;

        @media screen and (max-width: 1200px) {
          padding-left: 2em;
          padding-right: 2em;
        }
      }
      .form-group {
        margin-bottom: 6px !important;
      }

      input {
        border-radius: 0px !important;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        outline: none;
        box-shadow: none;
        padding: 1em;
        font-size: 12px;
        font-weight: lighter;
        margin-top: 30px;

        &:focus {
          outline: none;
          border: none;
        }
      }

      .select_ {
        border-radius: 0px !important;
        height: 45px !important;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        font-size: 12px;
        margin-top: 20px;
        outline: none;
        color: #555;
        box-shadow: none;
      }

      button.register {
        background-color: $studyblue;
        border: none;
        outline: none;
        width: 100%;
        margin: 0 auto;
        padding: 0.8em 2em;
        color: white;
        margin-top: 1em;
        font-weight: bold;
        text-transform: uppercase;
      }
    }
  }
}

.reg-text {
  top: 40px;
  position: relative;
  padding: 1em 6em 0em 6em;
}

.reg-login {
  background-color: transparent;
  border: none;
  color: $studyblue;
  outline: none;

  &:focus {
    outline: none;
    border: none;
  }
}
