﻿.login-page {
  .form-wrapper {
    max-width: 400px;
    width: 100%;

    form {
      background-color: $white;
      padding: 3em 2em;
      box-shadow: 0 4px 65px rgba(255, 255, 255, 0.07);

      label {
        display: block;
        font-weight: lighter;
      }

      .header {
        margin: 0px auto;
        font-weight: bold;
        text-align: center;
        font-family: $sans;
      }

      button {
        background-color: $studyblue;
        border: none;
        outline: none;
        width: 100%;
        margin: 0 auto;
        padding: 0.8em 2em;
        color: white;
        margin-top: 1em;
        font-weight: bold;
        text-transform: uppercase;
      }

      input {
        width: 100%;
        margin: 1em 0;
        padding: 0.7em;
        outline: none;
        border: 1px solid rgba(88, 88, 88, 0.4) !important;
      }

      .forgot-pass {
        padding: 0.5em 0;
        text-align: center;
        margin: 0 auto;

        span {
          display: inline-block;

          &:nth-child(2) {
            color: $studyblue;
            padding-left: 5px;
            font-weight: bold;
          }
        }
      }
    }

    .terms-support {
      margin-top: 1.5em;
      text-align: center;

      p {
        span {
          padding-right: 5px;

          a {
            text-decoration: none;
            color: #545454;
          }
        }
      }
    }
  }

  .newButton {
    text-align: center;
    display: inline-block;
  }

  .butt {
    width: 60%;
    padding-top: 3px;
  }
  .login-padding {
    margin-top: -40px;
    margin-bottom: 8%;
  }
  .choose {
    color: #10a3ff;
    font-weight: bold;
    font-size: 35px;
  }

  .learning {
    font-size: 22px;
  }

  .card-avatar {
    display: inline-block;
    margin-top: 15px;
    margin-right: 6px;
    margin-left: 6px;
    width: 130px;
    border: none;
    min-height: 130px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px;
    background-color: #fff;
  }

  .forced-margin {
    margin: 0 auto;
  }

  .login-items {
    margin-bottom: 11%;
    margin-top: 14%;
  }
}
