﻿.default-home {
  position: relative;
  margin: 0px auto;
  background-color: #fafafa;
  background-image: url("../../img/bg-home.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans" !important;

  .header-section {
    text-align: center;

    h2 {
      font-weight: bolder;
      color: $studyblue;
    }
  }

  .study-background {
    /**background-image: url("/images/studyBackground.png");
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain; */
    position: relative;
    margin-bottom: 6%;
    height: 70vh;
    font-family: $sans, "Open Sans"  !important;

    img {
      margin: 0px auto;
    }
  }
}
