.review-container {
  margin-top: 20px;
  margin-bottom: 30px;
  .review-button {
    background-color: #10a3ff;
    padding: 12px;
    margin-bottom: 1.5em;
    color: white;
    font-size: 16px;
    border-radius: 25px !important;
    width: 200px !important;
    font-weight: bolder;
    font-family: "work sans";
    &:hover {
      color: white !important;
    }
  }
  h3 {
    font-size: 20px;
    line-height: 1.5;
  }
}

.timeup-heading {
  font-weight: 700;
  font-size: 20px;
  margin-top: 0px;
  padding: 2.25rem;
  background-color: #10a3ff;
  color: #fff;
  border-top-left-radius: 10px !important;
  border-top-right-radius: 10px !important;
  border-bottom-right-radius: 0px !important;
  border-bottom-left-radius: 0px !important;
  border: #10a3ff;
  font-family: "work sans";
}

.timeup-text {
  line-height: 1.5;
  font-size: 18px;
  font-weight: 600;
  font-family: "work sans";
}
