﻿.quizContainer {
  position: relative;
  margin: 0px auto;
  background-color: #fff;
  background-image: url("../../img/bg-home.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100%;
  height: 88vh;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: "Open Sans" !important;
}
.quizQuestion {
  padding-top: 2em;
  font-size: 28px;
  font-weight: bolder;
  font-family: "Open Sans";
  color: "#5E5E5E";
  @media screen and (max-width: 768px) {
    font-size: 20px;
    margin-top: 1em;
  }

  .img-full {
    max-height: 200px;
  }

  .image-full {
    max-height: 200px;
  }
  .image-inline {
    display: inline;
    position: relative;
  }
}

.optionSection {
  padding-top: 3em;
  font-family: "Open Sans";
}

.optionSection .contain .checkmark::after {
  top: 9.3px;
  left: 9.5px;
  width: 20px;
  height: 20px;

  @media screen and (max-width: 768px) {
    top: 4.3px;
    left: 4.5px;
    width: 12px;
    height: 12px;
  }
}

.optionSection label {
  padding: 1em 0;
}

.col-lg-11 {
  .optionSection label span.question {
    position: relative;
    display: inline-block;
    padding-left: 6%;
    bottom: 10px;
    @media screen and (max-width: 768px) {
      bottom: 25px;
    }
  }
}

.col-lg-6 {
  .optionSection label span.question {
    position: relative;
    display: inline-block;
    padding-left: 10%;
    bottom: 10px;
  }
}

.optionSection .checkmark {
  height: 40px;
  width: 40px;
  @media screen and (max-width: 768px) {
    height: 20px;
    width: 20px;
  }
}

.optionSection input[type="radio"] {
  width: 50%;
}

.next-button {
  border-radius: 22px;
  padding-left: 35px;
  padding-top: 10px;
  padding-right: 35px;
  padding-bottom: 10px;
  background-color: #10a3ff;
  border: none;
  color: #fff;
  font-size: 14px;
  margin-right: 15px;
  @media screen and (max-width: 768px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.viewpassage-btn {
  color: $studyblue;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  left: 11%;
  top: -3%;
}

.viewpassage-btn:focus {
  outline: none !important;
  border: none;
}

.viewsection-btn {
  color: $studyblue;
  background-color: transparent;
  border-radius: 30px;
  padding: 0.5em;
  outline: none;
  font-size: 13px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top: 3%;
  right: 15%;
  border: 3px solid $studyblue;
}

.viewsection-btn1 {
  color: $studyblue;
  background-color: transparent;
  outline: none;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top: -3%;
  right: 15%;
  border: none;
}

.viewsection-btn:focus {
  outline: none !important;
}

.previous-button {
  border-radius: 22px;
  padding-left: 35px;
  padding-top: 10px;
  padding-right: 35px;
  padding-bottom: 10px;
  background-color: transparent;
  border: 1px solid #1e1e1e;
  color: $studyblue;
  font-size: 14px;
  margin-right: 15px;
  @media screen and (max-width: 768px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.next-button:focus {
  outline: none;
}
.previous-button:focus {
  outline: none;
}

.section-bottom {
  //position: fixed;
  z-index: 1001;
  bottom: 0;
  left: 0;
  color: black;
  margin: 0px auto;
  width: 100%;
  height: 90vh;
  transition: 0.5s;

  .containerr {
    padding: 3em;
    margin-top: 3em;
    font-size: 13px;
    color: black;
    text-align: justify;
    line-height: 2;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    height: 82vh;
    @media screen and (max-width: 768px) {
      margin-top: 3em;
      height: auto;
      padding: 0;
    }
  }

  @media screen and (max-width: 768px) {
    position: relative;
    height: auto;
  }
}

.section-hide {
  bottom: -5% !important;
}
.section-reset {
  bottom: 0 !important;
}

.decision-style_ {
  position: absolute;
  left: 0;
  bottom: -100%;
  z-index: 99999999;

  @media screen and (min-width: 768px) {
    position: fixed;
    top: 55%;
    left: 50%;
  }

  .decision-bg {
    background-color: white;
    padding: 1em;
    width: 100%;
    min-width: 320px;
  }
}

.section_view {
  width: 100%;
  max-width: 300px;
  background-color: transparent;
  border: 1px solid #001c5c;
  color: #001c5c;
  font-size: 15px;
  font-weight: bolder;
  padding: 0.7em 1.2em;
  // position: absolute;
  // bottom: 18%;
  // left: 17%;

  @media screen and (max-width: 768px) {
    bottom: 5%;
  }
}

.passage_view {
  width: 100%;
  max-width: 300px;
  background-color: transparent;
  border: 1px solid #001c5c;
  color: #001c5c;
  font-size: 15px;
  font-weight: bolder;
  padding: 0.7em 1.2em;
}

.wrapper-psg {
  background-color: white;
  height: 60vh;
  bottom: 54px;
  position: relative;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 50px;
  margin-top: 20px;
  overflow-y: scroll;

  @media screen and (max-width: 768px) {
    position: absolute;
    bottom: 90%;
    width: 100vw;
    right: -16%;
  }
}

.bg-white {
  background-color: white;
}
.bg-transparent {
  background-color: transparent;
}

.increase-zIndex {
  @media screen and (max-width: 768px) {
    z-index: 9999;
  }
}
.reduce-zIndex {
  @media screen and (max-width: 768px) {
    z-index: 0;
  }
}

.button-wrapper {
  background-color: white;
  padding: 2em;
  bottom: 18%;
  left: 13%;
  position: absolute;
  width: 80%;

  @media screen and (max-width: 768px) {
    width: 100%;
    position: relative;
    left: 0;
  }
}
